export const getGrammarQuestionsByCategory = (category: string): TestQuestion[] => {
  return grammarQuestions.filter(q => 
    q.category.toLowerCase() === category.toLowerCase()
  );
};

export const getGrammarQuestionsByType = (type: string): TestQuestion[] => {
  return grammarQuestions.filter(q => q.type === type);
};

export const getRandomGrammarQuestions = (count: number = 10): TestQuestion[] => {
  const shuffled = [...grammarQuestions].sort(() => Math.random() - 0.5);
  return shuffled.slice(0, count);
};

export const getAvailableGrammarCategories = (): string[] => {
  const categories = grammarQuestions.map(q => q.category);
  return [...new Set(categories)];
};

export const getAvailableGrammarTypes = (): string[] => {
  const types = grammarQuestions.map(q => q.type);
  return [...new Set(types)];
};

// Export stats for debugging
export const getGrammarStats = () => {
  const categories = getAvailableGrammarCategories();
  const types = getAvailableGrammarTypes();
  
  const categoryStats = categories.map(cat => ({
    category: cat,
    count: getGrammarQuestionsByCategory(cat).length
  }));
  
  const typeStats = types.map(type => ({
    type,
    count: getGrammarQuestionsByType(type).length
  }));
  
  return {
    total: grammarQuestions.length,
    categories: categoryStats,
    types: typeStats
  };
};
